<template>
  <div id="Footer">
      <span>LastError: {{LastError}}</span>
      <span>Since last sync: {{SinceLastSync}}</span>
      <span>Daily error count: {{DailyErrorCount}}</span>
  </div>
</template>

<script>
export default {
  props: ['LastError', 'LastSyncStr', 'DailyErrorCount'],
  name: 'Footer',
  components: {
  },
  data: () => ({
    SinceLastSync: '',
    timer: '',
  }),
  mounted() {
    this.timer = setInterval(this.calculateTimeLeft, 1000);
  },
  methods: {
    calculateTimeLeft() {
      this.SinceLastSync = `${parseInt((new Date() - new Date(this.LastSyncStr)) / 1000, 10)}s`;
    },
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },

};
</script>

<style lang="scss" scoped>
#Footer {
    display: flex;
    justify-content: space-between;
    color: #8E8EC1;
    font-size: 11px;
    padding-left: 10px;
    padding-right: 5px;

    & > * {
        margin: 5px;
    }
}
</style>
